import { Container } from "pixi.js";
import { Camera } from "./Camera";
import { vec2 } from "gl-matrix";
import { ObjectLayout } from "./Define";
import { ObjectType } from "./LevelDefine";

export abstract class BaseObject extends Container {

    private camera: Camera | null = null;
    protected _gamePosition: vec2 = [0, 0];
    public readonly type: ObjectType = ObjectType.null;
    public readonly layout: ObjectLayout = ObjectLayout.object;

    constructor(camera: Camera | null) {
        super();
        this.camera = camera;
    }

    //PUBLIC
    public get gamePosition(): vec2 {
        return this._gamePosition;
    }
    public set gamePosition(value: vec2) {
        this.customSetGamePosition(value);
    }

    public updateDrawPosition() {
        const drawPos = this.camera?.calculateDrawPosition(this.gamePosition) ?? [0, 0];
        this.position.set(drawPos[0], drawPos[1]);
    }

    public abstract isStateInactive(): boolean;
    public abstract setStateInactive(): void

    //PRIVATE
    protected customSetGamePosition(value: vec2) {
        vec2.copy(this._gamePosition, value);
    }


}