import { DisplayObject } from "pixi.js";
import { BaseObject } from "./BaseObject";
import { Camera } from "./Camera";
import { CellSize, ObjectLayout } from "./Define";
import { createGraphicRect } from "./Utilities";
import { ObjectType } from "./LevelDefine";

enum State {
    Inactive,
    Active,
}
export class Platform extends BaseObject {
    private state = State.Inactive;
    public readonly type = ObjectType.platform;
    public readonly layout = ObjectLayout.platform;

    constructor(camera: Camera) {
        super(camera);
        this.zIndex = -1;
        //
        this.addChild(createGraphicRect(CellSize, CellSize, 0x00ff00) as DisplayObject);
        //
        this.setState(State.Inactive);
    }

    private setState(value: State) {
        //end old state
        switch (this.state) {
            case State.Inactive:
                this.visible = true;
                break;
        }
        //start new state
        this.state = value;
        switch (this.state) {
            case State.Inactive:
                this.visible = false;
                break;
            case State.Active:
                break;
        }
    }

    //PUBLIC
    public isStateInactive(): boolean {
        return this.state === State.Inactive;
    }
    public setStateInactive(): void {
        this.setState(State.Inactive);
    }

    public setStateActive() {
        this.setState(State.Active);
    }


}