import { Application, Container, DisplayObject, FederatedPointerEvent, Rectangle } from "pixi.js";
import { Camera } from "./Camera";
import { Player } from "./Player";
import { CanvasSize } from "./Define";
import { ObjectsControl } from "./ObjectsControl";
import { BaseObject } from "./BaseObject";

enum State {
    Inactive,
    Start,
    Playing,
    Win,
    Lose,
    End,
}

class MazeGame {
    private application: Application;
    private state: State = State.Inactive;
    private world: Container;
    private camera: Camera;
    private player: Player;
    private objectsControl: ObjectsControl;

    constructor() {
        this.application = new Application({
            resolution: Math.min(window?.devicePixelRatio ?? 1, 2),
            backgroundAlpha: 0,
        });
        // this.application.ticker.speed = 2;
        this.application.ticker.maxFPS = 40;
        this.application.ticker.minFPS = 30;
        this.application.ticker.stop();
        this.application.ticker.add(this.update.bind(this));
        this.world = new Container();
        this.camera = new Camera();
        this.player = new Player(this.camera);
        this.objectsControl = new ObjectsControl(this.world, this.camera, this.player);
        //
        this.application.stage.addChild(this.world as DisplayObject);
        this.world.addChild(this.player as DisplayObject);
        this.world.addChild(this.camera as DisplayObject);
        //
        this.setState(State.Inactive);
    }

    private setState(value: State) {
        //end old state
        switch (this.state) {
            case State.Playing:
                this.application.stage.eventMode = "none";
                this.application.stage.off('pointerdown', this.onPointerDown);
                this.application.stage.off('pointerup', this.onPointerUp);
                break;
        }
        //start new state
        this.state = value;
        switch (this.state) {
            case State.Inactive:
                break;
            case State.Start:
                this.application.ticker.start();
                this.objectsControl.start();
                break;
            case State.Playing:
                this.objectsControl.setStatePlaying();
                this.application.stage.eventMode = "static";
                this.application.stage.on('pointerdown', this.onPointerDown, this);
                this.application.stage.on('pointerup', this.onPointerUp, this);
                break;
            case State.Win:
                break;
            case State.Lose:
                break;
            case State.End:
                this.application.ticker.stop();
                break;
        }
    }

    private update(dtFrame: number) {
        const dt = dtFrame / 60;
        // console.log(dt);
        switch (this.state) {
            case State.Start:
                if (true) {
                    this.setState(State.Playing);
                }
                break;
            case State.Playing:
                this.objectsControl.update(dt);
                //
                this.updateDrawPosition();
                this.sortObjects();
                break;
            case State.Win:
                break;
            case State.Lose:
                break;
            case State.End:
                break;
        }
    }

    //PUBLIC
    public updateParentDiv(parent: HTMLDivElement) {
        parent.appendChild(this.application.view as HTMLCanvasElement);
    }
    public onResize() {
        const windowWidth = Math.max(window.innerWidth, window.innerHeight);
        const windowHeight = Math.min(window.innerWidth, window.innerHeight);
        const width = CanvasSize.width;
        const height = CanvasSize.width / windowWidth * windowHeight;
        this.application.renderer.resize(width, height);
        this.application.stage.hitArea = new Rectangle(0, 0, width, height);
        this.world.position.set(0.5 * width, 0.5 * height);
    }

    public start() {
        this.setState(State.Start);
    }
    public end() {
        this.setState(State.End);
    }

    //PRIVATE
    private updateDrawPosition() {
        //update draw position
        this.world.children.forEach((child) => { (child as BaseObject)?.updateDrawPosition?.(); })
    }
    private sortObjects() {
        (this.world.children as BaseObject[]).sort((a, b) => {
            return (a.layout != b.layout) ?
                (a.layout - b.layout)
                : (a.y != b.y) ? (a.y - b.y) : (a.x - b.x)
        })
    }

    private onPointerDown(event: FederatedPointerEvent) {
        this.player.onPointerDown(event);
    }
    private onPointerUp(event: FederatedPointerEvent) {
        this.player.onPointerUp(event);
    }

}

export const mazeGame = new MazeGame();