import { DisplayObject, FederatedPointerEvent, Sprite } from "pixi.js";
import { BaseObject } from "./BaseObject";
import { Camera } from "./Camera";
import { Direction, ImageUrl } from "./Define";
import { convertXYOfPointerEvent } from "./Utilities";
import { vec2 } from "gl-matrix";
import { ObjectType } from "./LevelDefine";
import gsap from "gsap";

enum State {
    Inactive,
    Start,
    Respawn,
    Idle,
    Move,
}
export class Player extends BaseObject {

    public type: ObjectType = ObjectType.player;
    private state: State = State.Inactive;
    private sprite: Sprite;
    private pointerDownPos: vec2 = [0, 0];
    private moveDirection: Direction = Direction.Down;
    private moveTargetPos: vec2 = [0, 0];
    private map: number[][] = [];
    private respawnPos: vec2 = [0, 0];

    constructor(camera: Camera) {
        super(camera);
        this.sprite = Sprite.from(ImageUrl.player);
        this.sprite.anchor.set(0.5);
        //
        this.addChild(this.sprite as DisplayObject);
        //
        this.setState(State.Inactive);
    }

    private setState(value: State) {
        //end old state
        switch (this.state) {
            case State.Inactive:
                this.visible = true;
                break;
        }
        //start new state
        this.state = value;
        switch (this.state) {
            case State.Inactive:
                this.visible = false;
                break;
            case State.Start:
                break;
            case State.Respawn:
                this.gamePosition = this.respawnPos;
                break;
            case State.Idle:
                break;
            case State.Move:
                const target = this.findMoveTargetPos();
                if (target) {
                    this.moveTargetPos = target;
                    gsap.killTweensOf(this.gamePosition);
                    gsap.to(this.gamePosition, {
                        0: this.moveTargetPos[0],
                        1: this.moveTargetPos[1],
                        duration: 0.3,
                        ease: "none",
                        onUpdate: () => {
                            this.gamePosition = this.gamePosition;//to call set gameposition
                        },
                        onComplete: () => {
                            this.setState(State.Move);
                        }
                    });
                }
                else {
                    this.setState(State.Idle);
                }
                break;
        }
    }

    public update(dt: number) {
        switch (this.state) {
            case State.Respawn:
                if (true) {
                    this.setState(State.Idle);
                }
                break;
        }
    }

    //OVERRIDE
    protected customSetGamePosition(value: vec2) {
        const mapWidth = this.map?.[0]?.length ?? 1;
        const mapHeight = this.map?.length ?? 1;
        const x = (value[0] + mapWidth) % mapWidth;
        const y = (value[1] + mapHeight) % mapHeight;
        vec2.copy(this._gamePosition, [x, y]);
    }

    //PUBLIC
    public start() {
        this.setState(State.Start);
    }
    public setStateRespawn() {
        this.setState(State.Respawn);
    }

    public isStateInactive(): boolean {
        return this.state === State.Inactive;
    }
    public setStateInactive(): void {
        this.setState(State.Inactive);
    }

    public onPointerDown(event: FederatedPointerEvent) {
        switch (this.state) {
            case State.Idle:
            case State.Move:
                vec2.copy(this.pointerDownPos, convertXYOfPointerEvent(event));
                break;
        }
    }
    public onPointerUp(event: FederatedPointerEvent) {
        switch (this.state) {
            case State.Idle:
            case State.Move:
                const pointerUpPos = convertXYOfPointerEvent(event);
                const offset = vec2.sub(vec2.create(), pointerUpPos, this.pointerDownPos);
                this.moveDirection = this.calcDirection(offset);
                this.setState(State.Move);
                break;
        }
    }

    public setMap(map: number[][]) {
        this.map = map;
    }
    public setRespawnPos(pos: vec2) {
        vec2.copy(this.respawnPos, pos);
    }

    //PRIVATE
    private calcDirection(offset: vec2): Direction {
        const x = offset[0];
        const y = offset[1];
        if (Math.abs(x) >= Math.abs(y)) {
            return x >= 0 ? Direction.Right : Direction.Left;
        } else {
            return y >= 0 ? Direction.Down : Direction.Up;
        }
    }

    private findMoveTargetPos() {
        const mapWidth = this.map?.[0]?.length ?? 1;
        const mapHeight = this.map?.length ?? 1;
        let offset: vec2;
        switch (this.moveDirection) {
            case Direction.Up: offset = [0, -1]; break;
            case Direction.Down: offset = [0, 1]; break;
            case Direction.Left: offset = [-1, 0]; break;
            case Direction.Right: offset = [1, 0]; break;
        }
        const target = vec2.add(vec2.create(), this.gamePosition, offset);
        const isTargetValid = this.map?.[(target[1] + mapHeight) % mapHeight]?.[(target[0] + mapWidth) % mapWidth] ?? ObjectType.null > ObjectType.null;
        return isTargetValid ? target : null;
    }

}