import styled from "styled-components";
import { AbsoluteFullScreen } from "../general/GeneralCss";
import { useLayoutEffect, useRef, useState } from "react";
import { mazeGame } from "./MazeGame";

export const GameView = () => {
    const mainRef = useRef<HTMLDivElement>(null);
    const [isCallStartGame, setIsCallStartGame] = useState(true);

    //check to add canvas
    useLayoutEffect(() => {
        if (!mainRef?.current) return;
        mazeGame.updateParentDiv(mainRef.current);
    }, [mainRef]);

    //check to resize
    useLayoutEffect(() => {
        if (!mainRef?.current) return;
        mazeGame.onResize();
        window.addEventListener("resize", mazeGame.onResize.bind(mazeGame));
        return () => {
            window.removeEventListener("resize", mazeGame.onResize.bind(mazeGame));
        }
    }, [mainRef])

    //check to start game, end game
    useLayoutEffect(() => {
        if (!mainRef?.current || !isCallStartGame) return;
        mazeGame.start();
        setIsCallStartGame(false);
    }, [mainRef, isCallStartGame]);
    useLayoutEffect(() => {
        return () => {
            mazeGame.end();
        }
    }, []);

    return <Main ref={mainRef}>

    </Main>;
};

const Main = styled.div`
    ${AbsoluteFullScreen}
    > canvas{
       ${AbsoluteFullScreen}
    }
`;