import styled from "styled-components";
import { AbsoluteFullScreen, FlexColumn } from "../general/GeneralCss";
import { MazeBg } from "./MazeBg";
import { GameView } from "./GameView";
import { BackButton } from "../general/GeneralComponent";
import { useNavigate } from "react-router-dom";

export const MazeGameView = () => {
    const navigate = useNavigate();
    const level = 1;

    const onBack = () => {
        navigate(-1);
    }

    return <Main>
        <MazeBg level={level} />
        <GameView />
        <Top>
            <Title>Maze Game</Title>
            <CustomBackButton onClick={onBack} />
        </Top>
    </Main>;
}

const Main = styled.div`
    ${AbsoluteFullScreen}
`;

const Top = styled.div`
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    ${FlexColumn}
`;
const Title = styled.div`
    color: #000;
    position: relative;
`;

const CustomBackButton = styled(BackButton)`
    position: absolute;
    top: 5px;
    left: 5px;
`;