import { useNavigate, useRouteError } from 'react-router-dom';
import styled from 'styled-components';
import { AbsoluteFullScreen, BackgroundTransparent, FlexColumn, VerticalScroll } from './GeneralCss';

export const UnexpectedErrorPopup = () => {
    //useNavigate of react-router-dom
    const navigate = useNavigate();
    const error = useRouteError();
    console.log('error', error, typeof error);
    let errorText = '';
    try {
        errorText = JSON.stringify({
            error: {
                ...(error ?? {}),
                message: (error as any)?.message,
                stack: (error as any)?.stack
            }
        });
    } catch (error) {
        console.log('stringify error', error);
    }

    const onBack = () => {
        navigate(-1);
    };

    const onCta = () => {
        // console.log('status', (error as any)?.status);
        switch ((error as any)?.status) {
            case 404:
                onBack();
                break;
            default:
                window.location.reload();
                break;
        }
    };

    return <Main>
        <Title>Có lỗi xảy ra</Title>
        <Desc>Có lỗi xảy ra vui lòng thử lại!</Desc>
        <Error>{errorText}</Error>
        <CtaBtn onClick={onCta}>
            <CtaText>Thử lại</CtaText>
        </CtaBtn>
    </Main>;
};

const Main = styled.div`
    ${AbsoluteFullScreen}
    ${BackgroundTransparent}
    ${FlexColumn}
    color: #fff;
`;

const Title = styled.div`
`;
const Desc = styled.div`
`;
const Error = styled.div`
    width: 70%;
    max-height: 200px;
    ${VerticalScroll}
    margin: 20px 0 20px;
    font-size: 12px;
`;

const CtaBtn = styled.div`
    border: 1px solid #fff;
    padding: 10px 20px;
`;
const CtaText = styled.div`

`; 