import { vec2 } from "gl-matrix";
import { BaseObject } from "./BaseObject";
import { CellSize } from "./Define";
import { createGraphicRect } from "./Utilities";
import { DisplayObject } from "pixi.js";

export class Camera extends BaseObject {

    constructor() {
        super(null);
        this.position.set(0, 0);
        //
        // this.addChild(createGraphicRect(10, 10, 0xff0000) as DisplayObject);
    }

    //PUBLIC
    public isStateInactive(): boolean {
        return false;
    }
    public setStateInactive(): void {
    }

    public calculateDrawPosition(gamePos: vec2): vec2 {
        const x = (gamePos[0] - this.gamePosition[0]) * CellSize;
        const y = (gamePos[1] - this.gamePosition[1]) * CellSize;
        return [x, y];
    }


}