import styled from "styled-components";
import { AbsoluteFullScreen, FlexColumn } from "../general/GeneralCss";
import { useNavigate } from "react-router-dom";
import { RouteName } from "../general/Define";

export const Map = () => {
    const navigate = useNavigate();

    const onMazeGame = () => {
        navigate(RouteName.mazeGame);
    }

    return <Main>
        <Top>
            <Title>Map</Title>
        </Top>
        <Center>
            <PlayGameBtn onClick={onMazeGame}>
                <div>Maze game</div>
            </PlayGameBtn>
        </Center>
    </Main>
}

const Main = styled.div`
    ${AbsoluteFullScreen}
    ${FlexColumn}
`;
const Top = styled.div`
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    ${FlexColumn}
`;
const Title = styled.div`
    color: #000;
`;

const Center = styled.div`

`;
const PlayGameBtn = styled.div`
    border: 1px solid #000;
    padding: 10px 20px;
`;