import { Container, DisplayObject } from "pixi.js";
import { Camera } from "./Camera";
import { Player } from "./Player";
import { vec2 } from "gl-matrix";
import { BaseObject } from "./BaseObject";
import { Platform } from "./Platform";
import { LevelDefine, ObjectType } from "./LevelDefine";

enum State {
    Inactive,
    Start,
    Playing,
}

export class ObjectsControl {

    private state: State = State.Inactive;
    private world: Container;
    private camera: Camera;
    private player: Player;
    private currentMap: number[][] = [];
    private objects: BaseObject[] = [];

    constructor(world: Container, camera: Camera, player: Player) {
        this.world = world;
        this.camera = camera;
        this.player = player;
        //
        this.setState(State.Inactive);
    }

    private setState(value: State) {
        this.state = value;
        switch (this.state) {
            case State.Inactive:
                this.inactiveAllObjects();
                break;
            case State.Start:
                this.inactiveAllObjects();
                this.currentMap = LevelDefine.level1;
                this.camera.gamePosition = [((this.currentMap?.[0]?.length ?? 0) - 1) / 2, ((this.currentMap?.length ?? 0) - 1) / 2];
                this.player.setMap(this.currentMap);
                this.currentMap.forEach((row, rowIndex) => {
                    row.forEach((cellValue, colIndex) => {
                        const gamePos: vec2 = [colIndex, rowIndex];
                        switch (cellValue) {
                            case ObjectType.waterDrop:
                                break;
                            case ObjectType.knowledgePiece:
                                break;
                            case ObjectType.player:
                                this.player.setRespawnPos(gamePos);
                                this.player.start();
                                break;
                            case ObjectType.enemy1:
                                break;
                            case ObjectType.enemy2:
                                break;
                            case ObjectType.enemy3:
                                break;
                            case ObjectType.enemy4:
                                break;
                        }
                        switch (cellValue) {
                            case ObjectType.null:
                                break;
                            default:
                                {
                                    // //add platform
                                    // let obj = this.getObject(ObjectType.platform) as Platform;
                                    // obj.gamePosition = gamePos;
                                    // obj.setStateActive();
                                }
                                break;

                        }
                    })
                })
                break;
            case State.Playing:
                this.player.setStateRespawn();
                break;
        }
    }

    public update(dt: number) {
        switch (this.state) {
            case State.Start:
                break;
            case State.Playing:
                this.player.update(dt);
                break;
        }
    }

    //PUBLIC
    public start() {
        this.setState(State.Start);
    }
    public setStatePlaying() {
        this.setState(State.Playing);
    }

    //PRIVATE
    private inactiveAllObjects() {
        this.objects.forEach(obj => obj.setStateInactive());
    }

    private getObject(type: ObjectType) {
        let obj = this.objects.find(obj => (obj.type === type && obj.isStateInactive()));
        if (!obj) {
            switch (type) {
                case ObjectType.platform:
                default:
                    obj = new Platform(this.camera);
                    break;
            }
            this.objects.push(obj);
            this.world.addChild(obj as DisplayObject);
        }
        return obj;
    }

};
