import styled from "styled-components";
import { FixedFullScreen } from "./GeneralCss";
import { useLayoutEffect, useRef, useState } from "react";


export const UseDetectRotateLandscape = ({ children }: { children?: any }) => {
    const [isRotate, setIsRotate] = useState(window.innerWidth < window.innerHeight);
    useLayoutEffect(() => {
        const onResize = () => {
            setIsRotate(window.innerWidth < window.innerHeight);
        };
        window.addEventListener("resize", onResize);
        return () => {
            window.removeEventListener("resize", onResize);
        }
    }, []);
    return <UseDetectRotateLandscapeMain style={{
        transform: isRotate ? "translate(-50%, -50%) rotate(90deg)" : "translate(-50%, -50%)",
        // width: isRotate ? "100dvh" : "100dvw",
        // height: isRotate ? "100dvw" : "100dvh",
        width: "100dvmax",
        height: "100dvmin",
    }}>
        {children}
    </UseDetectRotateLandscapeMain>;
};
const UseDetectRotateLandscapeMain = styled.div`
    ${FixedFullScreen}
    top: 50%;
    left: 50%;
`;