import styled from "styled-components";
import { AbsoluteFullScreen, FlexColumn } from "../general/GeneralCss";
import { Image } from "../general/GeneralComponent";
import { CanvasSize, CellSize, ImageUrl } from "./Define";

export const MazeBg = ({ level }: { level: number }) => {
    const arrSrc = ['', ImageUrl.map1, ImageUrl.map3, ImageUrl.map5];
    const src = arrSrc?.[level] ?? '';
    return <Main>
        <MazeImage src={src} />
    </Main>;
};

const Main = styled.div`
    ${AbsoluteFullScreen}
    ${FlexColumn}
`;
const MazeImage = styled(Image)`
    /* width: calc(${CellSize}*29/${CanvasSize.width}*100vmax);
    height: calc(${CellSize}*21/${CanvasSize.width}*100vmax);
    background-size: 100% 100%; */

    width: 100%;
    height: 100%;
    background-size: calc(${CellSize}*29/${CanvasSize.width}*100dvmax) calc(${CellSize}*21/${CanvasSize.width}*100dvmax);
    background-position: center;
    background-repeat: no-repeat;
`;