import { vec2 } from "gl-matrix";
import { FederatedPointerEvent, Graphics } from "pixi.js";

export const createGraphicRect = (width: number, height: number, color: number): Graphics => {
    const graphic = new Graphics();
    // graphic.beginFill(color);
    graphic.lineStyle(2, color);
    graphic.drawRect(-0.5 * width, -0.5 * height, width, height);
    // graphic.endFill();
    return graphic;
}

export const convertXYOfPointerEvent = (pointer: FederatedPointerEvent): vec2 => {
    const isRotate = window.innerWidth < window.innerHeight;
    const clientX = pointer?.clientX ?? 0;
    const clientY = pointer?.clientY ?? 0;
    const x = isRotate ? clientY : clientX;
    const y = isRotate ? window.innerWidth - clientX : clientY;
    return [x, y];
}