import { Outlet, Route, RouterProvider, createBrowserRouter, createRoutesFromElements } from "react-router-dom";
import { UseDetectRotateLandscape } from "./general/UseUtilities";
import { RouteName } from "./general/Define";
import { Map } from "./map/Map";
import { MazeGameView } from "./mazeGame/MazeGameView";
import { UnexpectedErrorPopup } from './general/UnexpectedErrorPopup';

export const App = () => {
    const router = createBrowserRouter(
        createRoutesFromElements(
            <Route path="/" element={<Root />} errorElement={<UnexpectedErrorPopup />}>
                <Route path="" element={<Map />} />
                <Route path={RouteName.map} element={<Map />} />
                <Route path={RouteName.mazeGame} element={<MazeGameView />} />
            </Route>
        ),
        {
            basename: '/web-game/save-the-water'
        }
    );
    return <RouterProvider router={router} />;
}

const Root = () => {
    return <UseDetectRotateLandscape>
        <Outlet />
    </UseDetectRotateLandscape>;
}
